
.index_page__info_section {
  background-color: #fff;
  text-align: center;
  position: fixed;
  top: 88px;
  left: 0;
  right: 0;
  padding-top: 20px;
  padding-bottom: 30px;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
}
.container {
  max-width: 1320px;
  margin: auto;
  padding: 0 30px;
}
.container:after {
  content: "";
  visibility: hidden;
  display: block;
  height: 0;
  clear: both;
}
.index_page__logo_container {
  margin-bottom: 30px;
}
.index_page__logo_link {
  display: inline-block;
  color: #2673e7;
  text-decoration: none;
  -webkit-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}
.index_page__logo_link:hover {
  opacity: 0.7;
}
.index_page__logo_box {
  margin: auto;
  width: 32px;
}
.index_page__logo_img {
  width: 100%;
}
.index_page__logo_text {
  display: inline-block;
  width: 100px;
  height: 19px;
  text-indent: 101%;
  overflow: hidden;
  background: url("data:image/svg+xml;base64,") no-repeat center/100% auto;
}
.inde_page__info_container {
  margin-bottom: 30px;
}
.index_page__info {
  font-size: 10.4px;
  line-height: 1.4;
  color: #6a6969;
}
.index_page__info_paragraph:not(:last-child) {
  margin-bottom: 10px;
}
.index_page__info_link {
  position: relative;
  color: inherit;
  font-weight: 700;
  text-decoration: none;
  -webkit-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}
.index_page__info_link:before {
  content: '';
  opacity: 0;
  position: absolute;
  left: 0;
  right: 0;
  bottom: -4px;
  width: 100%;
  height: 2px;
  background-color: #6a6969;
}
.index_page__info_link:hover:before {
  opacity: 1;
  -webkit-transform: none;
          transform: none;
}
.index_page__info__break {
  display: none;
}


