
.index_page__content_section {
  position: relative;
  z-index: 1;
  padding: 50px 0 70px;
  background-color: #f6f6f6;
}
.gradient {
  position: relative;
  float: left;
  width: calc(33.3333% - 20px);
  height: 420px;
  background-color: #fff;
  margin-right: 30px;
  margin-bottom: 50px;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 6px 15px rgba(36,37,38,0.08);
  -webkit-transition: box-shadow 0.25s ease;
  transition: box-shadow 0.25s ease;
}
.gradient:hover {
  box-shadow: 5px 12px 20px rgba(36,37,38,0.13);
}
.gradient__title {
  position: absolute;
  left: 36px;
  top: 29px;
  font-size: 0.8125em;
  color: #333435;
  letter-spacing: 0.03em;
}
.gradient__download_button {
  position: absolute;
  z-index: 1;
  top: 28px;
  right: 26px;
  padding-right: 30px;
  text-indent: -5px;
  font-size: 13px;
  color: transparent;
  text-decoration: none;
  transtion: color 0.2s ease, text-indent 0.2s ease;
}
.gradient__download_button:before {
  content: '';
  position: absolute;
  right: 0;
  bottom: 0;
  top: 0;
  margin: 0;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 1px solid #383839;
  background: url("data:image/svg+xml;base64,") no-repeat center/8px auto;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}
.gradient__download_button:hover {
  color: #383839;
  text-indent: 0;
}
.gradient__download_button:hover:before {
  border-color: transparent;
  background-color: #fff;
  box-shadow: 2px 3.5px 5px rgba(51,52,53,0.16);
}
.gradient__background {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  margin: auto;
  border-radius: 50%;
  width: 200px;
  height: 200px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  cursor: url("data:image/svg+xml;base64,"), pointer;
}
.gradient__colors_box {
  position: absolute;
  left: 35px;
  bottom: 34px;
}
.gradient__advanced_text {
  position: absolute;
  left: 35px;
  bottom: 34px;
  font-size: 0.8125em;
  color: #333435;
}
.gradient__color {
  font-size: 0.8125em;
  color: #333435;
}
.gradient__arrow_symbol {
  margin: 0 8px 0 10px;
  font-weight: 700;
}
.gradient__copy_button {
  position: absolute;
  right: 30px;
  bottom: 31px;
  font-size: 0.8125em;
  color: #333435;
  letter-spacing: 0.03em;
  cursor: pointer;
}
.gradient__copy_button:before,
.gradient__copy_button:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -2px;
  height: 1px;
  width: 13%;
  background-color: #333435;
  -webkit-transition: opacity 0.25s ease, width 0.3s cubic-bezier(0.68, 0.05, 0.46, 1.02), left 0.2s 0.2s ease-out, -webkit-transform 0.25s ease;
  transition: opacity 0.25s ease, width 0.3s cubic-bezier(0.68, 0.05, 0.46, 1.02), left 0.2s 0.2s ease-out, -webkit-transform 0.25s ease;
  transition: opacity 0.25s ease, transform 0.25s ease, width 0.3s cubic-bezier(0.68, 0.05, 0.46, 1.02), left 0.2s 0.2s ease-out;
  transition: opacity 0.25s ease, transform 0.25s ease, width 0.3s cubic-bezier(0.68, 0.05, 0.46, 1.02), left 0.2s 0.2s ease-out, -webkit-transform 0.25s ease;
}
.gradient__copy_button:after {
  z-index: 10;
  width: 30%;
  background-color: #fff;
  opacity: 0;
}
.gradient__copy_button:hover:before {
  width: 100%;
}
.gradient__copy_button:hover:after {
  opacity: 1;
  left: 100%;
}
@media (min-width: 1025px) {
.gradient:nth-of-type(3n) {
    margin-right: 0;
}
}


